import React, {useState, useEffect} from 'react'
import { connect } from 'react-redux'
import {Badge, Card, Table} from 'antd';
import HeroComponents from "components/layout-components/CustomrPageHeaderAlt";
import ContainerTailwind from "components/layout-components/ContainerTailwind";
import ButtonPermission from "components/permission-components/button.permission";
import ApiService from "../../../../../../../services/api.service";
import AudioBtn from "../../../../../../../components/shared-components/audio-btn";
import {COLOR_DEFAULT_SELECTION} from "../../../../../../../configs/AppConfig";


/**
 *
 * @param {Object} props
 * @param {Object} props.list
 * @param {Function} props.GET_LIST_MD_THIN_CLIENT
 * @returns {JSX.Element}

 */
function Index(props){
    const [ loading, setLoading ] = useState(false)
    const [ pagination, setPagination ] = useState({
        page:1,
        limit:10
    })
    const [ data, setData ] = useState([])
    const [ params, setParams ] = useState({
        page:1,
        limit:10
    })

    useEffect(()=> {
        setLoading(true)
        new ApiService({
            url:`/api/v1/audio`,
            config : {
                params: {
                    ...params
                }
            }
        }).get()
            .then((response)=> {
                setData([...response?.data])
                setPagination({
                    ...pagination,
                    ...response?.pagination,
                    ...response?.paginate
                })
                setLoading(false)
            })
            .catch((err)=> {
                setData([])
                setLoading(false)
            })
    },[params])


    const columns = [
        {
            dataIndex:"id",
            title:"No",
            render:(_,val,index)=> index + 1
        },
        {
            dataIndex:"identifier",
            title:"Filename"
        },
        {
            dataIndex:['audio_type','data','name'],
            title:"Audio Type",
            render:(_,val,index)=> {
                return <Badge color={val?.audio_type?.data?.color ?? COLOR_DEFAULT_SELECTION} text={_}/>
            }
        },
        {
            dataIndex:['language','data','name'],
            title:"Language",
            render: (_,val,index)=> {
                return <Badge color={val?.language?.data?.color ?? COLOR_DEFAULT_SELECTION} text={[val?.language?.data?.code ?? "",_].join(val?.language?.data?.code ? " | " : "")}/>
            }
        },
        {
            dataIndex:['mime_type'],
            title:"Mime Type"
        },
        {
            dataIndex:"result",
            render:(_,val)=> {
                return <AudioBtn
                    url={[process.env.REACT_APP_API_MASTER,_].join('')}
                    title={val?.identifier}
                    onClick={(callback)=> {
                        callback()
                    }}
                />
            }
        },
    ]

    return (
        <React.Fragment>
            <HeroComponents
                name={"Overview"}
                positionName={['Master Data','Bank Audio']}
                thumbnail={null}
            />

            <div style={{marginTop:90}}>
                <ContainerTailwind>
                    <Card>
                        <Table
                            loading={loading ?? false}
                            columns={columns}
                            dataSource={data}
                            pagination={pagination?.total_items < params?.limit ? false : {
                                pageSize: params?.limit,
                                total: pagination?.total_items ?? 0,
                                onChange: (page, pageSize) => {
                                    setParams({
                                        ...params,
                                        page,
                                        limit: pageSize
                                    })
                                }
                            }}

                        />
                    </Card>

                </ContainerTailwind>
            </div>
        </React.Fragment>
    )
}

Index.propTypes = {}
Index.defaultProps = {}

export default connect(({MasterData})=> {
    let { thin_client } = MasterData
    let { list } = thin_client
    return { list }
},{})(React.memo(Index))